<template>
  <v-container>
    <template>
      <v-simple-table dense class="my_class">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">Date Process</th>
              <th class="text-left">From</th>
              <th class="text-left">Action Taken</th>
              <th class="text-left">To</th>
              <th class="text-left">Remarks</th>
              <th class="text-left">Status</th>
              <th class="text-left">Attachments</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, i) in newHistoryData" :key="i">
              <td width="150px">{{ convertDate(item[0].createdDate) }}</td>

              <td width="240px">{{ item[0].sender_name }}</td>
              <td
                :class="` font-weight-light pr-4 ${setColor2(
                  item[0].action_taken
                )}--text`"
                width="100px"
              >
                <b>{{ item[0].action_taken }} to</b>
              </td>
              <td width="240px">
                {{
                  item[0].action_taken == "Assign"
                    ? item[0].group_name
                    : item[0].receiver_name
                }}
              </td>
              <td width="750px">
                <p>{{ item[0].remarks }}</p>
              </td>
              <td width="180px">
                <v-chip
                  :color="setColor(item[0].status)"
                  class="ma-2 ml-0 white--text"
                  small
                >
                  {{ item[0].status_name }}
                </v-chip>
              </td>
              <td>
                <v-menu
                  offset-y
                  dense
                  max-height="200"
                  v-if="item[0].files.length > 0"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="mx-1"
                      color="info"
                      v-bind="attrs"
                      v-on="on"
                      depressed
                      x-small
                    >
                      View
                      <v-icon small>mdi-menu-down-outline</v-icon>
                    </v-btn>
                  </template>
                  <v-list dense class="py-0">
                    <v-list-item
                      dense
                      v-for="(itemfiles, ifiles) in item[0].files"
                      :key="ifiles"
                      :href="itemfiles.file_path_name[0]['new_name']"
                      target="_blank"
                      :download="itemfiles.file_path_name[0]['new_name']"
                    >
                      <!-- :download="itemfiles.file_path_name[0]['new_name']" -->
                      {{ itemfiles.file_path_name[0]["orig_name"] }}
                    </v-list-item>
                  </v-list>
                </v-menu>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </template></v-container
  >
</template>
  </v-container>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  props: ["header_id"],
  data: () => ({
    name: "HistoryViewModal",

    items: [
      {
        color: "red",
        icon: "mdi-star",
        year: "1960",
      },
      {
        color: "purple",
        icon: "mdi-book-variant",
        year: "1970",
      },
      {
        color: "green",
        icon: "mdi-airballoon",
        year: "1980",
      },
      {
        color: "indigo",
        icon: "mdi-buffer",
        year: "1990",
      },
    ],
    monthNames: [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ],
    newHistoryData: [],
  }),
  created() {
    this.fetchHistory({
      header_id: this.header_id,
    }).then(() => {
      this.fetchAllTypAttach({
        header_id: this.header_id,
      }).then(() => {
        this.newHistoryData = this.allHistory[0];
        this.setAttachment();
      });
    });
  },
  methods: {
    ...mapActions(["fetchHistory", "fetchAllTypAttach"]),
    setColor2(res) {
      if (res == "Submit") {
        return "green";
      } else if (res == "Forward") {
        return "blue";
      } else if (res == "Return") {
        return "red";
      } else if (res == "Assign") {
        return "deep-orange";
      } else if (res == "Return To LGU") {
        return "red";
      }
    },
    setColor(res) {
      if (res == "1") {
        return "green";
      } else if (res == "2") {
        return "blue";
      } else if (res == "3") {
        return "teal  ";
      } else if (res == "4") {
        return "brown";
      } else if (res == "5") {
        return "brown";
      }
    },
    convertDate(date) {
      const date2 = new Date(date);
      //const month = date.toLocaleString("default", { month: "long" });
      //return date.getMonth(long);
      return (
        this.monthNames[date2.getMonth()] +
        " " +
        date2.getDate() +
        ", " +
        date2.getFullYear()
      );
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    setAttachment() {
      let newAllAttach = JSON.parse(JSON.stringify(this.allTypAttach));
      let count = this.newHistoryData.length;

      for (let i = 0; i < count; i++) {
        this.newHistoryData[i] = [this.newHistoryData[i]].map((e, i) => ({
          ...e,
          files: newAllAttach
            .filter((e2) => parseInt(e.route_order) === parseInt(e2.route) + 1)
            .map((e, i) => ({
              ...e,
              file_path_name: JSON.parse(e.file_path_name).map((e3, i) => ({
                ...e3,
                new_name: `${this.globalBasepath}/swms_api/public/typ_file/${e.reference_number}/others/${e3.new_name}`,
                new_name: this.globalFileChangeNameOthers(
                  this.globalBasepath,
                  e.reference_number,
                  e3.new_name
                ),
              })),
            })),
        }));
      }

      //console.log(this.newHistoryData);
    },
  },
  computed: { ...mapGetters(["allHistory", "allTypAttach", "globalBasepath"]) },
};
</script>