<template>
  <v-container class="pa-1">
    <!-- <v-toolbar dense color="cyan" dark>
      <v-toolbar-title>Form Two Evaluation</v-toolbar-title>
    </v-toolbar> -->
    <v-card dense class="mx-auto card-padding pa-4">
      <template>
        <v-dialog
          v-model="dialogAdd"
          persistent
          max-width="1000px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      v-model="limitRow"
                      dense
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn> -->
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn class="float-right" small @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <!-- <span class="headline">{{ formTitle }}</span> -->
            </v-card-title>
            <v-card-text>
              <small>
                <span class="blue--text"
                  >*Please fill-in the required fields before processing.</span
                ></small
              >
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-container>
                  <v-row>
                    <v-col>
                      <v-select
                        :items="actionChoice"
                        label="Action"
                        item-text="name"
                        return-object
                        v-model="switch1"
                        @change="getItemText"
                        dense
                        outlined
                        :rules="requiredField"
                        required
                      >
                      </v-select>
                      <v-select
                        v-if="switch1.value == 0"
                        :items="actionChoice2"
                        label="Internal Action"
                        item-text="name"
                        return-object
                        v-model="switch2"
                        dense
                        outlined
                        :rules="requiredField"
                        required
                      >
                      </v-select>
                      <v-select
                        item-text="text"
                        clearable
                        label="Personnel"
                        return-object
                        :items="allPersonnel"
                        v-model="cmbPersonnel"
                        :rules="requiredField"
                        dense
                        outlined
                        required
                      ></v-select>
                      <v-textarea
                        outlined
                        dense
                        label="Remarks"
                        v-model="remarks"
                        :rules="requiredField"
                        required
                      >
                      </v-textarea>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col>
                      <small>
                        <span class="black--text">Attachments: </span>
                        <span class="blue--text">
                          (max. size of 70 MB per attachment)</span
                        ><span class="red--text"> -required</span>
                        <br />
                        <span class="blue--text">
                          * please upload attachment(s) before saving /
                          processing *</span
                        ></small
                      >
                      <v-row>
                        <v-col md="12">
                          <v-btn
                            color="info"
                            class="mr-2"
                            small
                            outlined
                            @click="$refs.refInputEl.click()"
                          >
                            <v-icon left> mdi-plus </v-icon>
                            <span class="d-none d-sm-block">Add Files</span>
                          </v-btn>

                          <input
                            ref="refInputEl"
                            type="file"
                            accept=".jpeg,.png,.jpg,GIF"
                            :hidden="true"
                            multiple
                            @change="onFileChange"
                          />
                          <v-btn outlined color="info" small>
                            <v-icon left> mdi-cloud-upload </v-icon>
                            Upload All
                          </v-btn>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in uploads"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn
                                    @click="uploadSpecificFile(item, i)"
                                    icon
                                  >
                                    <v-icon color="blue lighten-1"
                                      >mdi-upload</v-icon
                                    >
                                  </v-btn>
                                  <v-btn @click="removeFile(i)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-close-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <small>
                            <span class="black--text"
                              >Current Attachments:
                            </span>
                          </small>
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in convertFile(allTypAttach)"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.file_path_name[0].orig_name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn @click="deleteFile(item)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-delete-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
              <!-- <small>*indicates required field</small> -->
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="
                  dialogAdd = false;
                  clearinput();
                "
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="confirmSubmit">{{
                buttonTitle
              }}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Second modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View History Modal -->
      <template>
        <v-dialog
          v-model="dialogViewHistory"
          max-width="1700px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewHistory == true">
                <HistoryViewModal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table class="mini-margin-top my_class">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th class="display-none">#</th>
                <th>City</th>
                <th>Reference Number</th>
                <th>Status</th>
                <th>Sender</th>
                <th>Remarks</th>
                <th>Action Needed</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <v-menu offset-y dense>
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            class="mx-1"
                            color="info"
                            depressed
                            x-small
                          >
                            <v-icon small>{{
                              res.internal_action == "0"
                                ? "mdi-archive-check-outline"
                                : "mdi-archive-eye-outline"
                            }}</v-icon>
                          </v-btn>
                        </template>
                        <span>{{
                          res.internal_action == "0" ? "Evaluate" : "View"
                        }}</span>
                      </v-tooltip>
                    </template>
                    <v-list dense class="py-0">
                      <v-list-item dense @click="getEditForm2(res)">
                        Form 2
                      </v-list-item>
                      <v-list-item dense @click="getEditForm3(res)">
                        Form 3</v-list-item
                      >
                    </v-list>
                  </v-menu>

                  <v-menu offset-y dense>
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            class="mx-1"
                            color="info"
                            depressed
                            x-small
                          >
                            <v-icon small>mdi-printer-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>Print</span>
                      </v-tooltip>
                    </template>
                    <v-list dense class="py-0">
                      <v-list-item dense @click="getPrintForm2(res)">
                        Form 2
                      </v-list-item>
                      <v-list-item dense @click="getPrintForm3(res)">
                        Form 3</v-list-item
                      >
                    </v-list>
                  </v-menu>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res)"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          dialogViewHistory = true;
                        "
                      >
                        <v-icon small>mdi-history</v-icon>
                      </v-btn>
                    </template>
                    <span>History</span>
                  </v-tooltip>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td width="150px">{{ res.city_name }}</td>
                <td width="150px">{{ res.reference_number }}</td>
                <td>{{ res.status_name }}</td>
                <td width="210px">{{ res.sender_name }}</td>
                <td>{{ res.remarks }}</td>
                <td>
                  <v-chip
                    v-if="res.status != 5"
                    :color="checkInternalAction(res.internal_action).color1"
                    class="ma-2 ml-0"
                  >
                    {{ checkInternalAction(res.internal_action).desc1 }}
                  </v-chip>
                </td>

                <!-- <td class="pl-4" width="150px">
                  <input
                    color="success"
                    type="checkbox"
                    @change="form4upload(res)"
                    :v-model="convertInt(res.form4_upload)"
                    :checked="convertInt(res.form4_upload)"
                    :disabled="convertInt(res.form4_upload)"
                  />
                </td> -->
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <vue-html2pdf
          :show-layout="false"
          :float-layout="true"
          :enable-download="false"
          :preview-modal="true"
          :paginate-elements-by-height="1400"
          filename="hee hee"
          :pdf-quality="2"
          :manual-pagination="false"
          pdf-format="a4"
          pdf-orientation="portrait"
          pdf-content-width="800px"
          ref="html2Pdf"
        >
          <!-- PRIIIINT -->
          <section slot="pdf-content" id="printMe">
            <!-- PDF Content Here -->
            <!-- <div id="pageHeader" class="text-h6 right">
          EMED-ESWMS-F003 <br />Rev. 1/10-15-2018
        </div> -->
            <div class="text-h6 center" style="text-align: right">
              EMED-ESWMS-F003 <br />Rev. 1/10-15-2018
            </div>
            <div class="text-h6 center">Regional and SWMD Evaluator's Use</div>
            <div id="formTitle" class="text-h6 center">
              Evaluation Checklist
            </div>
            <div id="formTitle2" class="text-h6 center">
              Municipality of: BANI, PANGASINAN
            </div>
            <div id="myTable2">
              <table
                class="text-caption"
                dense
                id="myTable"
                style="
                  border-collapse: collapse;
                  border: 1px solid black;
                  width: 98%;
                "
              >
                <thead>
                  <tr>
                    <th rowspan="2" class="text-left">Parameter/Indicator</th>
                    <th colspan="2" class="text-left">Compliance</th>
                    <th rowspan="2" class="text-left">Remarks</th>
                  </tr>
                  <tr>
                    <th class="text-left">Yes</th>
                    <th class="text-left">No</th>
                  </tr>
                </thead>
                <tbody id="tableBody">
                  <!-- 1 -->
                  <tr>
                    <td><b>1. Final Disposal Site </b></td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <!-- 1.1 -->
                  <tr>
                    <td>Open/Controlled dumpsite ceased operation</td>
                    <td>
                      <!-- <v-icon v-if="indicator[0].radio1 == 0">mdi-check</v-icon> -->
                      <v-icon v-if="indicator[0].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[0].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 1.2 -->
                  <tr>
                    <td>Safe Closure and Rehabilitation Submitted</td>
                    <td>
                      <v-icon v-if="indicator[0].radio2 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio2 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[0].remarks2 }}</p>
                    </td>
                  </tr>
                  <!-- 1.3 -->
                  <tr>
                    <td>
                      <p>Sanitary landfill</p>
                      <br />
                      <table id="innerTable" dense>
                        <thead>
                          <tr>
                            <th class="text-left"></th>
                            <th class="text-left">Yes</th>
                            <th class="text-left">No</th>
                            <th class="text-left">Not Applicable</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>Memorandum of Agreement</td>
                            <td>
                              <p v-if="indicator[0].radio4 == 1">
                                <b></b>
                              </p>
                            </td>

                            <td>
                              <p v-if="indicator[0].radio4 == 2">
                                <b></b>
                              </p>
                            </td>
                            <td>
                              <p v-if="indicator[0].radio4 == 3">
                                <b></b>
                              </p>
                            </td>
                          </tr>
                          <tr>
                            <td>Contract</td>
                            <td>
                              <p v-if="indicator[0].radio5 == 1">
                                <b></b>
                              </p>
                            </td>

                            <td>
                              <p v-if="indicator[0].radio5 == 2">
                                <b></b>
                              </p>
                            </td>
                            <td>
                              <p v-if="indicator[0].radio5 == 3">
                                <b></b>
                              </p>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio3 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio3 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[0].remarks3 }}</p>
                    </td>
                  </tr>
                  <!-- 1.4 -->
                  <tr>
                    <td>
                      <p>Residual Containment Area</p>
                      <table id="innerTable" dense>
                        <tbody>
                          <tr>
                            <td>Capacity</td>
                            <td>{{ indicator[0].capacity }}</td>
                          </tr>
                          <tr>
                            <td>Location</td>
                            <td>{{ indicator[0].location }}</td>
                          </tr>
                          <tr>
                            <td>Life Span</td>
                            <td>{{ indicator[0].lifeSpan }}</td>
                          </tr>
                        </tbody>
                      </table>
                      <p>
                        Future disposal area (please check the appropriate box)
                      </p>
                      <table id="innerTable" dense>
                        <thead>
                          <tr>
                            <td>Owned</td>
                            <td>Cluster</td>
                          </tr>
                        </thead>

                        <tbody>
                          <tr>
                            <td>Location: {{ indicator[0].fdLocation }}</td>
                            <td></td>
                          </tr>
                          <tr>
                            <td>Life Span: {{ indicator[0].lifeSpan }}</td>
                            <td></td>
                          </tr>
                          <tr>
                            <td>Status: {{ indicator[0].fdStatus }}</td>
                            <td></td>
                          </tr>
                        </tbody>
                      </table>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio6 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[0].radio6 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[0].remarks4 }}</p>
                    </td>
                  </tr>
                  <!-- 2 -->
                  <tr>
                    <td><b>2. Materials Recovery Facilities </b></td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <!-- 2.1 -->
                  <tr>
                    <td>
                      <p>
                         At the end of the planning period, <br />
                        all barangays should be serviced by MRF
                      </p>
                      <p>
                         If utilizing centralized MRF, define which
                        <br />brgys. are covered by the centralized MRF
                        <br />(e.g. 2 brgys/15 total brgys)
                      </p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[1].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[1].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[1].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 3 -->
                  <tr>
                    <td><b>3.Diversion Targets</b></td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <!-- 3.1 -->
                  <tr>
                    <td>
                      <p>Above 50% by year</p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[2].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[2].radio1 == 1"></v-icon>
                    </td>
                    <td rowspan="2">
                      <p>{{ indicator[2].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 3.2 -->
                  <tr>
                    <td>
                      <p>
                        Increasing diversion targets and decreasing disposal
                        targets
                      </p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[2].radio2 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[2].radio2 == 1"></v-icon>
                    </td>
                  </tr>
                  <!-- 4 -->
                  <tr class="page-break">
                    <td>
                      <b>4. Segregation at Source and Segregated Collection</b>
                    </td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <!-- 4.1 -->
                  <tr>
                    <td>
                      <p>100% target for the 10th year planning period</p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[3].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[3].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[3].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 5 -->
                  <tr>
                    <td>
                      <b>5. Expenditures/Investment</b>
                    </td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                  <!-- 5.1 -->
                  <tr>
                    <td>
                      <p>
                        Presence of appropriate projected investment costs
                        for<br />
                        the strategies identified
                      </p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[4].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[4].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[4].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 5.2 -->
                  <tr>
                    <td>
                      <p>Source of Fund is indicated</p>
                    </td>
                    <td>
                      <v-icon v-if="indicator[4].radio2 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[4].radio2 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[4].remarks2 }}</p>
                    </td>
                  </tr>
                  <!-- 6 -->
                  <tr>
                    <td>
                      <b>6. Presence of policy support</b>
                    </td>
                    <td>
                      <v-icon v-if="indicator[5].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[5].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[5].remarks1 }}</p>
                    </td>
                  </tr>
                  <!-- 7 -->
                  <tr>
                    <td>
                      <b
                        >7. Presence of institutional/organizational
                        arrangements
                      </b>
                    </td>
                    <td>
                      <v-icon v-if="indicator[6].radio1 == 0"></v-icon>
                    </td>
                    <td>
                      <v-icon v-if="indicator[6].radio1 == 1"></v-icon>
                    </td>
                    <td>
                      <p>{{ indicator[6].remarks1 }}</p>
                    </td>
                  </tr>
                </tbody>
              </table>
