      "deleteFileVuex",
      "updateForm4",
    ]),
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.stat1,
        new_app: 1,
        action: this.getCondition,
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.stat1,
        new_app: 1,
        action: this.getCondition,
      });
    },
    limitFunction() {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.stat1,
        new_app: 1,
        action: this.getCondition,
      });
    },
    getSearch() {
      this.searchPlanList({
        txtsearch: this.Search,
