        status: this.stat1,
        new_app: 1,
        action: this.getCondition,
      });
    },
    getDelete(res) {
      this.dialogDelete = true;
      this.id = res.id;
    },
    getEditForm2(res) {
      sessionStorage.setItem("typ_header_id", res.id);
      sessionStorage.setItem("form_id", res.form_id);
      sessionStorage.setItem("year", res.year);
      sessionStorage.setItem("reference_number", res.reference_number);
      console.log(res.reference_number);
      if (res.internal_action == 0) {
        sessionStorage.setItem("formAction", 1);
      } else {
        sessionStorage.setItem("formAction", 2);
      }
      this.$router.push({
        path: "formtwo",
        // query: {
        //   header_id: res.id,
        //   form_id: res.form_id,
        //   year: res.year,
        // },
      });
    },
    getEditForm3(res) {
      sessionStorage.setItem("typ_header_id", res.id);
      sessionStorage.setItem("form_id", res.form_id3);
      sessionStorage.setItem("year", res.year);
      sessionStorage.setItem("reference_number", res.reference_number);
      this.$router.push({
        path: "formthree",
      });
    },
    getPrintForm2(res) {
      if (res.form_id != null) {
        this.fetchForm2({
          id: res.form_id,
        })
          .then(() => {
            this.setForm2Details();
          })
          .then(() => {
            this.printForm2();
          });
      } else {
        swal({
          title: "No Evaluation",
          icon: "warning",
        });
      }
    },
    getPrintForm3(res) {
      if (res.form_id3 != null) {
        this.fetchForm3({
          id: res.form_id3,
        })
          .then(() => {
            this.setForm3Details();
          })
          .then(() => {
            this.printForm3();
          });
      } else {
        swal({
          title: "No Evaluation",
          icon: "warning",
        });
      }
    },

    //Form 4 uploads
    form4upload(res) {
      this.editedIndex = res.id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.id = res.id;
      this.route_order = parseInt(res.route_order);

      this.updateForm4({
        id: this.editedIndex,
        receiver: {
          value: res.created_by,
          text: res.created_by_name,
          region: res.region_id,
        },
        remarks: "Upload Form 4",
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: res.status,
        route_order: this.route_order + 1,
        action_taken: "Assign Form 4 Upload",
        internal_action: 0,

        date: this.getNow(),
        action: this.getCondition,
        search_status: this.stat1,

        form4Input: 1,
      });
    },
    convertInt(string) {
      return string == "1" ? true : false;
    },

    // SET DATA TO STATE
    setForm2Details() {
      let newAllChapter = JSON.stringify(this.allForm2);
      let new_data = JSON.parse(newAllChapter);
      if (new_data != null) {
        this.indicator[0].radio1 = new_data.indicator_com_1_1;
        this.indicator[0].radio2 = new_data.indicator_com_1_2;
        this.indicator[0].radio3 = new_data.indicator_com_1_3;
        this.indicator[0].radio4 = new_data.memo_agreement;
        this.indicator[0].radio5 = new_data.contract;
        this.indicator[0].radio6 = new_data.indicator_com_1_4;
        this.indicator[0].radio7 = new_data.disposal_area;
        this.indicator[0].remarks1 = new_data.indicator_rem_1_1;
        this.indicator[0].remarks2 = new_data.indicator_rem_1_2;
        this.indicator[0].remarks3 = new_data.indicator_rem_1_3;
        this.indicator[0].remarks4 = new_data.indicator_rem_1_4;

        this.indicator[0].capacity = new_data.capacity;
        this.indicator[0].location = new_data.location;
        this.indicator[0].fdLocation = new_data.fdLocation;
        this.indicator[0].lifeSpan = new_data.lifeSpan;
        this.indicator[0].fdStatus = new_data.fdStatus;

        this.indicator[1].radio1 = new_data.indicator_com_2_1;
        this.indicator[1].remarks1 = new_data.indicator_rem_2_1;

        this.indicator[2].radio1 = new_data.indicator_com_3_1;
        this.indicator[2].radio2 = new_data.indicator_com_3_1;
        this.indicator[2].remarks1 = new_data.indicator_rem_3_1;

        this.indicator[3].radio1 = new_data.indicator_com_4_1;
        this.indicator[3].remarks1 = new_data.indicator_rem_4_1;

        this.indicator[4].radio1 = new_data.indicator_com_5_1;
        this.indicator[4].radio2 = new_data.indicator_com_5_2;
        this.indicator[4].remarks1 = new_data.indicator_rem_5_1;
        this.indicator[4].remarks2 = new_data.indicator_rem_5_2;

        this.indicator[5].radio1 = new_data.indicator_com_6_1;
        this.indicator[5].remarks1 = new_data.indicator_rem_6_1;

        this.indicator[6].radio1 = new_data.indicator_com_7_1;
        this.indicator[6].remarks1 = new_data.indicator_rem_7_1;
      }
      return true;
    },
    setForm3Details() {
      let newAllChapter = JSON.stringify(this.allForm3);
      let new_data = JSON.parse(newAllChapter);

      this.info[0].item[0].itemModel = new_data.lguName;
      this.info[0].item[1].itemModel = new_data.receiveDate;
      this.info[0].item[2].itemModel = new_data.noBarangay;
      this.info[0].item[3].itemModel = new_data.popProjection;
      this.info[0].item[4].itemModel = new_data.areaHas;

      this.info[1].item[0].itemModel = new_data.organization;
      this.info[1].item[1].itemModel = new_data.numStaff;
      this.info[1].item[2].itemModel = new_data.colEquip;
      this.info[1].item[3].itemModel = new_data.mrf;
      this.info[1].item[4].itemModel = new_data.finalDisposal;
      this.info[1].item[5].itemModel = new_data.wasteQuan;
      this.info[1].item[6].itemModel = new_data.wasteCom;
      this.info[1].item[7].itemModel = new_data.expenditure;
      this.info[1].item[8].itemModel = new_data.annualBud;

      this.info[2].item[0].itemModel = new_data.sourceRed;
      this.info[2].item[1].itemModel = new_data.collection;
      this.info[2].item[2].itemModel = new_data.transfer;
