    },

    getHeaderEdit(res) {
      this.years();
      this.editedIndex = res.id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;
      this.dialogAdd = true;
      this.id = res.id;

      this.getTypAttach(res.id, res.route_order);
      this.route_order = parseInt(res.route_order);
      sessionStorage.setItem("reference_number", res.reference_number);
    },
    confirmSubmit(res) {
      if (this.$refs.form.validate()) {
        this.dialogSubmit = true;
      }
    },
    submitHeader() {
      let stat = 0;
      let internalAct = 0;
      if (this.switch1.name == "Forward") {
        stat = this.rowStatus;
        internalAct = this.switch2.value;
      } else if (this.switch1.name == "Submit") {
        stat = this.rowStatus + 1;
        internalAct = 0;
      } else {
        stat = this.rowStatus - 1;
        internalAct = 0;
      }
      this.SubmitHeaderPlan({
        id: this.editedIndex,
        receiver: this.cmbPersonnel,
        remarks: this.remarks,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: stat,
        route_order: this.route_order + 1,
        action_taken: this.switch1.name,
        internal_action: internalAct,

        date: this.getNow(),
        action: this.getCondition,
        search_status: this.stat1,
      });

      this.clearinput();
      this.dialogSubmit = false;
    },
    changeAction(var2) {},
    years() {
      let year = new Date().getFullYear();
      this.select_years = Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
      return Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
    },
    getItemText() {
      this.cmbPersonnel = "";
      this.remarks = "";

      let personelreg = -1;
      let personnelAccess = parseInt(sessionStorage.getItem("access"));
      let user_id = sessionStorage.getItem("iis_employee_number");
      let action = this.switch1.value;
      if (action == 2) {
        personelreg = this.user_region_id;
      } else {
        personelreg = sessionStorage.getItem("region_id");
      }
      this.fetchPersonnel({
        reg_id: personelreg,
        user_role: personnelAccess,
        action: action,
        user_id: user_id,
      });
    },
    checkInternalAction(action) {
      let desc = "";
      let color = "";
      if (parseInt(action) == 0) {
        desc = "For Evaluation";
        color = "success";
      } else if (parseInt(action) == 1) {
        desc = "For Review";
        color = "success";
      } else if (parseInt(action) == 2) {
        desc = "For Comment";
        color = "success";
      }

      return { desc1: desc, color1: color };
    },
    clearinput() {
      this.remarks = "";
      this.txtWasteGen = "";
      this.cmbPersonnel = "";
      this.switch1 = "";
      this.dialogAdd = false;
      this.editedIndex = -1;
      this.uploads = [];
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    printForm2() {
      this.$htmlToPaper("printMe");
    },
    printForm3() {
      this.$htmlToPaper("printMeForm3");
    },

    //File Upload
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      this.uploads = Object.entries(files).map((e, i) => e[1]);
    },

    removeFile(i) {
      this.uploads.splice(i, 1); // 2nd parameter means remove one item only
      console.log(this.uploads);
    },

    getTypAttach(id, route_order) {
      this.fetchTypAttach({
        header_id: id,
        route: route_order,
        user: sessionStorage.getItem("iis_employee_number"),
      }).then(() => {
        //this.setAttachment();
      });
    },

    uploadSpecificFile(item, i) {
      console.log(item);
      var formData = new FormData();
      formData.append("files[]", item);
      formData.append("header_id", this.editedIndex);
      formData.append("route", parseInt(this.route_order));
      formData.append("user", sessionStorage.getItem("iis_employee_number"));
      formData.append(
        "reference_number",
        sessionStorage.getItem("reference_number")
      );
      this.uploadTypPerLine(formData).then(() => {
        this.removeFile(i);
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    deleteFile(item) {
      console.log(item);
      this.deleteFileVuex(item).then(() => {
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    convertFile(data) {
      let newAllAttach = JSON.stringify(data);
      let new_data = JSON.parse(newAllAttach);
      new_data = new_data.map((e, i) => ({
        ...e,
        file_path_name: JSON.parse(e.file_path_name),
      }));
      return new_data;
    },
  },
  computed: {
    ...mapGetters([
      "allPlanList",
      "tgetCurrentrow",
      "regionId",
      "allPersonnel",
      "allForm2",
      "allForm3",
      "allTypAttach",
    ]),
    buttonTitle() {
      return this.switch1.value == 0
        ? "Forward"
        : this.switch1.value == 2
        ? "Return"
        : "Submit";
    },
  },
  created() {
    this.getRegionInfo().then(() => {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.stat1,
        new_app: 1,
        action: this.getCondition,
      });
    });
    // this.fetchRegions();
  },
};
</script>