              <v-row id="not-to-print" class="not-to-print">
                <v-col md="4">
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        small
                        color="primary"
                        dark
                        v-bind="attrs"
                        v-on="on"
                      >
                        Attachments
                      </v-btn>
                    </template>
                    <v-list>
                      <!-- :href="item.new_name" -->
                      <!-- v-if="item.ext == `pdf`" -->
                      <template v-for="(item, index) in upload.curr_data">
                        <v-list-item
                          :key="`fle` + index"
                          v-if="textFile.includes(item.ext)"
                          :href="item.new_name"
                          target="_blank"
                          :download="item.new_name"
                        >
                          <v-list-item-title>
                            {{ item.orig_name }}</v-list-item-title
                          >
                        </v-list-item>
                      </template>
                    </v-list>
