          wacsbioPer: "",
          wacsdis: "",
          wacsdisPer: "",
          wacsrec: "",
          wacsrecPer: "",
          wacsrwpd: "",
          wacsrwpdPer: "",
          wacsspec: "",
          wacsspecPer: "",
          wasteDis: "",
          wasteDisPer: "",
          wasteDiv: "",
          wasteDivPer: "",
          wasteGen: "",
        });

        //For table 761
        this.chapters[6].outline[17].outlineTable[0].tableData.push({
          year: event,
          projectedDis: "",
          capacityRes: "",
        });

        //For table 771
        this.chapters[6].outline[21].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: 0,
          collection: "",
          storageTreatment: "",
        });

        //For table 7711
        this.chapters[6].outline[22].outlineTable[0].tableData.push({
          year: event,
          estimatedQty: "",
          collection: "",
          storageTreatment: "",
        });

        //For table and edit form 1111
        this.chapters[10].outline[0].outlineTable[0].tableHeader.splice(
