    fetchchapters(header_id) {
      this.fetchAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.reference_number = this.allChapter[0]["reference_number"];
        for (var i = 1; i < 13; i++) {
          this.setChapterDetails(i);
        }
        this.fetchAttachement(header_id);
      });
    },

    fetchAttachement(header_id) {
      this.fetchAttachAllPerChapter({
        header_id: header_id,
      }).then(() => {
        this.setAttachment();
      });
    },

    filterHeader(headers) {
      var newArrObj = headers.filter(function (obj) {
        return obj.text !== "Actions";
      });
      return newArrObj;
    },
    nonNullItems(chapterIndex, outlineIndex, tableIndex) {
      let x = this.chapters[chapterIndex].outline[outlineIndex].outlineTable[
        tableIndex
      ].tableHeader
        .filter((e) => {
          return e.total;
        })
        .map((e) => {
          return e.total;
        });
      return x;
    },
    sumField(chapterBase, outline, outlineTable, key) {
      // sum data in give key (property)
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid) {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        return this.numberWithCommas(value.toFixed(2));
      }
    },

    sumFieldPer(chapterBase, outline, outlineTable, key) {
      var isValid = /^[0-9,.]*$/.test(key); //Setting index to number
      if (!isValid && key != "totalKg") {
        let value = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) => parseFloat(a) + (parseFloat(b[key].replace(/,/g, "")) || 0),
          0
        );

        let total = this.chapters[chapterBase].outline[outline].outlineTable[
          outlineTable
        ].tableData.reduce(
          (a, b) =>
            parseFloat(a) + (parseFloat(b["totalKg"].replace(/,/g, "")) || 0),
          0
        );
        let per = (value / total) * 100;

        return this.numberWithCommas(per.toFixed(2)) + "%";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    setChapterDetails(i) {
      let newAllChapter = [];
      newAllChapter = JSON.stringify(this.allChapter);
      if (newAllChapter.length >= 3) {
        var chapterBase = i - 1;

        if (i == 1) {
          let new_data = JSON.parse(newAllChapter)[0];
          this.chapters[chapterBase].outline[0].outlineModel =
            new_data.chap1Intro;
          this.chapters[chapterBase].outline[1].outlineModel = new_data.purpose;
          this.chapters[chapterBase].outline[2].outlineModel =
            new_data.approach;
          this.chapters[chapterBase].outline[3].outlineModel =
            new_data.acknowledgement;
        }
