<template>
  <v-container class="pa-1">
    <!-- <v-toolbar color="cyan" dark>
      <v-toolbar-title>Form Two Evaluation</v-toolbar-title>
    </v-toolbar> -->
    <v-card class="mx-auto card-padding pa-4">
      <!--Process Modal  -->
      <template>
        <v-dialog
          v-model="dialogAdd"
          persistent
          max-width="1000px"
          :retain-focus="false"
        >
          <template v-slot:activator="{}">
            <v-row>
              <v-col>
                <v-row>
                  <v-col>
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                </v-row>
                <!-- <v-btn color="primary" dark v-bind="attrs" v-on="on">
                  Add New
                </v-btn> -->
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title>
              <!-- <span class="headline">{{ formTitle }}</span> -->
            </v-card-title>
            <v-card-text>
              <small>
                <span class="blue--text"
                  >*Please fill-in the required fields before processing.</span
                ></small
              >
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-container>
                  <v-row>
                    <v-col>
                      <v-select
                        :items="actionChoice"
                        label="Action"
                        item-text="name"
                        return-object
                        v-model="switch1"
                        @change="getItemText"
                        dense
                        outlined
                        :rules="requiredField"
                        required
                      >
                      </v-select>
                      <v-select
                        v-if="switch1.value !== 0"
                        item-text="text"
                        clearable
                        label="Personnel"
                        return-object
                        :items="allPersonnel"
                        v-model="cmbPersonnel"
                        :rules="requiredField"
                        dense
                        outlined
                        required
                      ></v-select>
                      <v-select
                        v-if="switch1.value === 0"
                        item-text="text"
                        clearable
                        label="Group"
                        return-object
                        :items="allGroup"
                        v-model="assigned_group"
                        :rules="requiredField"
                        dense
                        outlined
                        required
                      ></v-select>
                      <v-textarea
                        outlined
                        dense
                        label="Remarks"
                        v-model="remarks"
                        :rules="requiredField"
                        required
                      >
                      </v-textarea>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col>
                      <small>
                        <span class="black--text">Attachments: </span>
                        <span class="blue--text">
                          (max. size of 70 MB per attachment)</span
                        ><span class="red--text"> -required</span>
                        <br />
                        <span class="blue--text">
                          * please upload attachment(s) before saving /
                          processing *</span
                        ></small
                      >
                      <v-row>
                        <v-col md="12">
                          <v-btn
                            color="info"
                            class="mr-2"
                            small
                            outlined
                            @click="$refs.refInputEl.click()"
                          >
                            <v-icon left> mdi-plus </v-icon>
                            <span class="d-none d-sm-block">Add Files</span>
                          </v-btn>
                          <!-- accept=".jpeg,.png,.jpg,GIF,.pptx" -->
                          <input
                            ref="refInputEl"
                            type="file"
                            accept=".jpeg,.png,.jpg,.pptx,.pdf"
                            :hidden="true"
                            multiple
                            @change="onFileChange"
                          />
                          <!-- <v-btn outlined color="info" small>
                            <v-icon left> mdi-cloud-upload </v-icon>
                            Upload All
                          </v-btn> -->
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in uploads"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn
                                    @click="uploadSpecificFile(item, i)"
                                    icon
                                  >
                                    <v-icon color="blue lighten-1"
                                      >mdi-upload</v-icon
                                    >
                                  </v-btn>
                                  <v-btn @click="removeFile(i)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-close-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <small>
                            <span class="black--text"
                              >Current Attachments:
                            </span>
                          </small>
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in convertFile(allTypAttach)"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.file_path_name[0].orig_name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn @click="deleteFile(item)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-delete-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
              <!-- <small>*indicates required field</small> -->
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="
                  dialogAdd = false;
                  clearinput();
                "
                >Close</v-btn
              >
              <v-btn color="blue darken-1" text @click="confirmSubmit">{{
                buttonTitle
              }}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Evaluation Modal -->
      <template>
        <v-dialog
          v-model="dialogView"
          persistent
          max-width="900px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <!-- <span class="headline">{{ formTitle }}</span> -->
            </v-card-title>
            <v-card-text>
              <div class="text-h5 mb-4">Evaluations</div>
              <v-expansion-panels>
                <v-expansion-panel
                  v-for="(data, chapterIndex) in allothersEval"
                  :key="chapterIndex"
                >
                  <v-expansion-panel-header>
                    {{ data.name }}
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 0">
                      <v-icon color="success" class="ml-4"
                        >mdi-checkbox-marked-circle</v-icon
                      >
                      Approved
                    </div>
                    <div class="text-subtitle-2" v-if="data.evaluator_app == 1">
                      <v-icon color="error" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      Disapproved
                    </div>
                    <div
                      class="text-subtitle-2"
                      v-if="data.evaluator_app == null"
                    >
                      <v-icon color="blue-grey" class="ml-4"
                        >mdi-close-circle</v-icon
                      >
                      No Evaluation
                    </div>
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-divider></v-divider>
                    <div class="text-subtitle-2 mb-4">Remarks</div>
                    {{
                      data.evaluator_remarks == null
                        ? "None"
                        : data.evaluator_remarks
                    }}
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
              <v-btn
                class="mt-2 mx-2"
                small
                color="primary"
                @click="dialogViewPlan = true"
                >10-Year Plan</v-btn
              >
              <v-menu offset-y dense>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"
                    v-on="on"
                    class="mt-2 mx-2"
                    small
                    color="primary"
                  >
                    Central Evaluation
                  </v-btn>
                </template>
                <v-list dense class="py-0">
                  <v-list-item
                    dense
                    @click="
                      eval_type = 0;
                      dialogViewForm2 = true;
                    "
                  >
                    Form 2
                  </v-list-item>
                  <v-list-item
                    dense
                    @click="
                      eval_type = 0;
                      dialogViewForm3 = true;
                    "
                  >
                    Form 3</v-list-item
                  >
                </v-list>
              </v-menu>
              <v-menu offset-y dense>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    v-bind="attrs"
                    v-on="on"
                    class="mt-2 mx-2"
                    small
                    color="primary"
                  >
                    Region Evaluation
                  </v-btn>
                </template>
                <v-list dense class="py-0">
                  <v-list-item
                    dense
                    @click="
                      eval_type = 1;
                      dialogViewForm2 = true;
                    "
                  >
                    Form 2
                  </v-list-item>
                  <v-list-item
                    dense
                    @click="
                      eval_type = 1;
                      dialogViewForm3 = true;
                    "
                  >
                    Form 3</v-list-item
                  >
                </v-list>
              </v-menu>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogView = false"
                >Close</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Return Confirmation Modal -->
      <template>
        <v-dialog v-model="dialogReturn" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to return ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogReturn = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="returnHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Confirmation Modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Plan Modal -->
      <template>
        <v-dialog
          v-model="dialogViewPlan"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewPlan">
                <TypViewModal :header_id="editedIndex" :typ_year="typ_year" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 2 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm2"
          max-width="1300px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm2 == true && eval_type == 0">
                <Form2Modal :header_id="editedIndex" :form_id="form2_id" />
              </template>
              <template v-if="dialogViewForm2 == true && eval_type == 1">
                <Form2Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Form 3 Modal -->
      <template>
        <v-dialog
          v-model="dialogViewForm3"
          max-width="1300px"
          :retain-focus="false"
          ><v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewForm3 == true && eval_type == 0">
                <Form3Modal :header_id="editedIndex" :form_id="form3_id" />
              </template>
              <template v-if="dialogViewForm3 == true && eval_type == 1">
                <Form3Modal :header_id="editedIndex" />
              </template>
            </v-card-text>
            <v-card-actions class="justify-end"> </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View History Modal -->
      <template>
        <v-dialog
          v-model="dialogViewHistory"
          max-width="1700px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewHistory == true">
                <HistoryViewModal :header_id="editedIndex" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE -->
      <template>
        <v-simple-table class="mini-margin-top my_class">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th class="display-none">#</th>
                <th>City</th>
                <th>Reference Number</th>
                <th>Submit By</th>
                <th>Remarks</th>
                <th>Assigned Evaluator</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.typ_header_id">
                <td>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="info"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderView(res)"
                      >
                        <v-icon small>mdi-archive-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>View</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res)"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          dialogViewHistory = true;
                        "
                      >
                        <v-icon small>mdi-history</v-icon>
                      </v-btn>
                    </template>
                    <span>History</span>
                  </v-tooltip>
                </td>
                <td class="display-none">{{ res.id }}</td>
                <td width="150px">{{ res.city_name }}</td>
                <td width="150px">{{ res.reference_number }}</td>
                <td width="210px">{{ res.sender_name }}</td>
                <td>{{ res.remarks }}</td>
                <td>
                  <v-chip
                    :color="res.group_name == null ? 'blue-grey' : 'success'"
                    class="ma-2 ml-0 white--text"
                  >
                    {{
                      res.group_name == null ? "Not Assigned" : res.group_name
                    }}
                  </v-chip>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
import Form2Modal from "../Include/form2_view";
import Form3Modal from "../Include/form3_view";
import HistoryViewModal from "../Include/history_view";
export default {
  name: "List",
  components: {
    TypViewModal,
    Form2Modal,
    Form3Modal,
    HistoryViewModal,
  },
  data: () => ({
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [5],

    dialogAdd: false,
    dialogView: false,
    dialogSubmit: false,
    dialogReturn: false,
    dialogViewPlan: false,
    dialogViewForm2: false,
    dialogViewForm3: false,
    dialogViewHistory: false,

    Search: "",

    user_region_id: "",
    region_num: "",
    cmbPersonnel: "",
    assigned_group: "",
    remarks: "",

    form2_id: 0,
    form3_id: 0,
    eval_type: 0,

    valid: true,

    editedIndex: -1,
    typ_year: 0,
    switch1: "",
    actionChoice: [
      {
        value: 1,
        name: "Submit",
      },
      {
        value: 0,
        name: "Assign",
      },
      {
        value: 2,
        name: "Return",
      },
    ],
    action_taken: "",
    requiredField: [(v) => !!v || "Field is required"],
    getCondition: "del",

    uploads: [],
    route_order: "",
  }),
  methods: {
    ...mapActions([
      "fetchPlanList",
      "fetchRegions",
      "fetchProvince",
      "fetchCities",
      "fetchPersonnel",
      "fetchGroup",
      "increment",
      "decrement",
      "searchPlanList",
      "returnPlantList",
      "SubmitHeaderPlan",
      "getOthersEval",
      "uploadTypPerLine",
      "fetchTypAttach",
      "deleteFileVuex",
    ]),

    // Table Function
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        action: this.getCondition,
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        action: this.getCondition,
      });
    },
    limitFunction() {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        action: this.getCondition,
      });
    },
    getSearch() {
      this.searchPlanList({
        txtsearch: this.Search,
        limit: this.limitRow,
        status: 4,
        new_app: 1,
        action: this.getCondition,
      });
    },

    // Modal Function
    getHeaderEdit(res) {
      this.editedIndex = res.id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;
      this.region_num = res.rgnnum;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;
      this.dialogAdd = true;

      this.getTypAttach(res.id, res.route_order);
      this.route_order = parseInt(res.route_order);
      sessionStorage.setItem("reference_number", res.reference_number);
    },
    getHeaderView(res) {
      this.editedIndex = res.id;
      this.user_region_id = res.region_id;
      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;
      this.region_num = res.rgnnum;

      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;
      this.form2_id = res.form_id;
      this.form3_id = res.form_id3;

      this.typ_year = res.year;
      console.log(res.assigned_group);
      if (res.assigned_group == null) {
        swal({
          title: "Assign Group first",
          icon: "warning",
        });
        this.dialogView = false;
      } else {
        this.getOthersEval({
          typ_id: res.id,
          group: res.assigned_group,
          condition: "all",
        });
        this.dialogView = true;
      }
    },
    confirmSubmit(res) {
      if (this.$refs.form.validate()) {
        this.dialogSubmit = true;
      }
    },
    confirmReturn(res) {
      this.dialogReturn = true;
    },

    //File Upload
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      this.uploads = Object.entries(files).map((e, i) => e[1]);
    },

    removeFile(i) {
      this.uploads.splice(i, 1); // 2nd parameter means remove one item only
      console.log(this.uploads);
    },

    getTypAttach(id, route_order) {
      this.fetchTypAttach({
        header_id: id,
        route: route_order,
        user: sessionStorage.getItem("iis_employee_number"),
      }).then(() => {
        //this.setAttachment();
      });
    },

    uploadSpecificFile(item, i) {
      console.log(item);
      var formData = new FormData();
      formData.append("files[]", item);
      formData.append("header_id", this.editedIndex);
      formData.append("route", parseInt(this.route_order));
      formData.append("user", sessionStorage.getItem("iis_employee_number"));
      formData.append(
        "reference_number",
        sessionStorage.getItem("reference_number")
      );
      this.uploadTypPerLine(formData).then(() => {
        this.removeFile(i);
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    deleteFile(item) {
      console.log(item);
      this.deleteFileVuex(item).then(() => {
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    convertFile(data) {
      let newAllAttach = JSON.stringify(data);
      let new_data = JSON.parse(newAllAttach);
      new_data = new_data.map((e, i) => ({
        ...e,
        file_path_name: JSON.parse(e.file_path_name),
      }));
      return new_data;
    },

    // Process Function
    submitHeader() {
      let stat = 0;
      if (this.switch1.value == 0) {
        stat = this.rowStatus;
      } else if (this.switch1.value == 1) {
        stat = this.rowStatus + 1;
      } else {
        stat = this.rowStatus - 1;
      }
      this.SubmitHeaderPlan({
        id: this.editedIndex,
        receiver: this.cmbPersonnel,
        remarks: this.remarks,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: stat,
        route_order: this.route_order + 1,
        action_taken: this.switch1.name,
        assigned_group: this.assigned_group.value,
        internal_action: 0,

        date: this.getNow(),
        action: this.getCondition,
        search_status: 4,
      });

      this.clearinput();
      this.dialogSubmit = false;
    },
    returnHeader() {
      this.SubmitHeaderPlan({
        id: this.editedIndex,
        receiver: this.cmbPersonnel,
        remarks: this.remarks,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: this.rowStatus - 1,
        route_order: this.route_order + 1,
        action_taken: "Return",
        date: this.getNow(),
        action: this.getCondition,
      });

      this.clearinput();
      this.dialogReturn = false;
    },
    getItemText() {
      this.cmbPersonnel = "";
      this.remarks = "";

      let personelreg = -1;
      let personnelAccess = parseInt(sessionStorage.getItem("access"));
      let user_id = sessionStorage.getItem("iis_employee_number");
      let action = this.switch1.value;

      personelreg = sessionStorage.getItem("region_id");
      console.log(this.switch1.value);
      if (this.switch1.value == 0) {
        this.fetchGroup({
          reg_id: this.region_num,
        });
      } else {
        this.fetchPersonnel({
          reg_id: personelreg,
          user_role: personnelAccess,
          action: action,
          user_id: user_id,
        });
      }
    },

    // Other Function
    clearinput() {
      this.remarks = "";
      this.txtWasteGen = "";
      this.cmbPersonnel = "";
      this.switch1 = "";
      this.dialogAdd = false;
      this.editedIndex = -1;
      this.uploads = [];
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
  },
  computed: {
    ...mapGetters([
      "allPlanList",
      "tgetCurrentrow",
      "regionId",
      "allPersonnel",
      "allGroup",
      "allothersEval",
      "allTypAttach",
      "globalBasepath",
      "getsession",
    ]),
    buttonTitle() {
      return this.switch1.value == 0
        ? "Assign"
        : this.switch1.value == 2
        ? "Return"
        : "Submit";
    },
  },
  created() {
    this.fetchPlanList({
      offset: this.tgetCurrentrow,
      limit: this.limitRow,
      status: 4,
      new_app: 1,
      action: this.getCondition,
    });

    console.log(this.getsession);
  },
};
</script>