<template>
  <v-container class="pa-1">
    <!-- <v-toolbar color="cyan" dark>
      <v-toolbar-title>10 YEAR PLAN HEADER</v-toolbar-title>
    </v-toolbar> -->
    <v-card class="mx-auto pa-4">
      <!-- Add NEW MODAL -->
      <template>
        <v-dialog v-model="dialogAdd" persistent width="unset">
          <template v-slot:activator="{ on, attrs }">
            <v-row>
              <v-col>
                <v-row>
                  <v-col md="6">
                    <v-text-field
                      dense
                      label="Search Bar"
                      v-model="Search"
                      @keyup="getSearch"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-combobox
                      dense
                      v-model="limitRow"
                      label="Show"
                      :items="allLimit"
                      @change="limitFunction"
                    >
                    </v-combobox>
                  </v-col>
                  <v-col>
                    <v-select
                      dense
                      v-model="cstatus"
                      label="Status"
                      :items="status"
                      @change="statusFunction"
                    >
                    </v-select>
                  </v-col>
                </v-row>
                <v-btn
                  small
                  v-if="checkStatus"
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on"
                  @click="addNew()"
                >
                  Add New Plan
                </v-btn>
              </v-col>
              <v-col>
                <!-- <v-btn @click="getSearch">Search</v-btn> -->
                <v-btn
                  small
                  class="float-right mini-margin-left"
                  @click="nextFunction"
                  >Next</v-btn
                >
                <v-btn small class="float-right" @click="prevFunction"
                  >Prev</v-btn
                >
                <input v-model="tgetCurrentrow" type="hidden" />
              </v-col>
            </v-row>
          </template>
          <v-card>
            <v-card-title> </v-card-title>
            <v-card-text>
              <v-form ref="form" v-model="valid" lazy-validation>
                <v-container>
                  <v-row>
                    <v-col>
                      <small>
                        <span class="black--text"
                          >10-Year Plan Basic Information:
                        </span></small
                      >
                      <v-select
                        label="Region"
                        :items="regionItem"
                        v-model="cmbRegion"
                        dense
                        outlined
                        @change="getProvince(cmbRegion)"
                        readonly
                        filled
                      ></v-select>
                      <v-select
                        label="Province"
                        :items="provinceItem"
                        v-model="cmbProvince"
                        @change="getCity"
                        dense
                        outlined
                        readonly
                        filled
                      ></v-select>
                      <v-select
                        label="City"
                        :items="cityItem"
                        v-model="cmbCity"
                        dense
                        outlined
                        readonly
                        filled
                      ></v-select>
                      <v-text-field
                        label="Reference Number"
                        v-model="txtReferenceNumber"
                        dense
                        outlined
                        readonly
                        filled
                      ></v-text-field>
                      <v-row>
                        <v-col cols="6">
                          <v-select
                            label="Year"
                            v-model="txtStartYear"
                            :readonly="editedIndex >= 0"
                            dense
                            outlined
                            placeholder="YYYY"
                            :items="select_years"
                            :rules="requiredField"
                            required
                          ></v-select>
                        </v-col>
                        <v-col cols="6">
                          <v-text-field
                            label="PCG"
                            v-model="txtPcg"
                            dense
                            outlined
                            :readonly="editedIndex >= 0"
                            @keyup="isNum(txtPcg, `txtPcg`, 1)"
                            :rules="requiredField"
                            required
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col>
                      <small>
                        <span class="black--text"
                          >Processing Information:
                        </span></small
                      >
                      <br />
                      <v-select
                        item-text="text"
                        label="Personnel"
                        return-object
                        :items="personnelItem"
                        v-model="cmbPersonnel"
                        :rules="requiredField"
                        dense
                        outlined
                        required
                        readonly
                        filled
                      ></v-select>

                      <v-textarea
                        outlined
                        auto-grow
                        dense
                        label="Remarks"
                        v-model="remarks"
                        :rules="requiredField"
                      >
                      </v-textarea>
                    </v-col>
                    <v-divider vertical v-if="editedIndex > 0"></v-divider>
                    <v-col v-if="editedIndex > 0">
                      <small>
                        <span class="black--text">Attachments: </span>
                        <span class="blue--text">
                          (max. size of 70 MB per attachment)</span
                        ><span class="red--text"> -required</span>
                        <br />
                        <span class="blue--text">
                          * please upload attachment(s) before saving /
                          processing *</span
                        ></small
                      >
                      <v-row>
                        <v-col md="12">
                          <v-btn
                            color="info"
                            class="mr-2"
                            small
                            outlined
                            @click="$refs.refInputEl.click()"
                          >
                            <v-icon left> mdi-plus </v-icon>
                            <span class="d-none d-sm-block">Add Files</span>
                          </v-btn>

                          <input
                            ref="refInputEl"
                            type="file"
                            accept=".jpeg,.png,.jpg,GIF"
                            :hidden="true"
                            multiple
                            @change="onFileChange"
                          />
                          <!-- <v-btn outlined color="info" small>
                            <v-icon left> mdi-cloud-upload </v-icon>
                            Upload All
                          </v-btn> -->
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in uploads"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn
                                    @click="uploadSpecificFile(item, i)"
                                    icon
                                  >
                                    <v-icon color="blue lighten-1"
                                      >mdi-upload</v-icon
                                    >
                                  </v-btn>
                                  <v-btn @click="removeFile(i)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-close-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col md="12" class="pt-0">
                          <small>
                            <span class="black--text"
                              >Current Attachments:
                            </span>
                          </small>
                          <div>
                            <v-list dense>
                              <!-- <v-list-item-group color="primary"> -->
                              <v-list-item
                                v-for="(item, i) in convertFile(allTypAttach)"
                                :key="i"
                              >
                                <!-- <v-list-item-icon>
                                  <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon> -->
                                <v-list-item-content>
                                  <v-list-item-title
                                    v-text="item.file_path_name[0].orig_name"
                                  ></v-list-item-title>
                                </v-list-item-content>
                                <v-list-item-action
                                  dense
                                  class="ma-0 d-flex flex-row"
                                >
                                  <v-btn @click="deleteFile(item)" icon>
                                    <v-icon color="red lighten-1"
                                      >mdi-delete-circle</v-icon
                                    >
                                  </v-btn>
                                </v-list-item-action>
                              </v-list-item>
                              <!-- </v-list-item-group> -->
                            </v-list>
                          </div>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="
                  dialogAdd = false;
                  clearinput();
                "
                >Close</v-btn
              >
              <v-btn
                v-if="this.editedIndex <= 0"
                color="blue darken-1"
                text
                @click="insertHeader"
                >Continue</v-btn
              >
              <v-btn
                v-if="this.dialogAction == '2' && this.editedIndex >= 0"
                color="blue darken-1"
                text
                @click="updateHeader"
                >Update</v-btn
              >
              <v-btn
                v-if="this.dialogAction == '3' && this.editedIndex >= 0"
                color="blue darken-1"
                text
                @click="confirmSubmit"
                >{{ buttonTitle }}</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Modal Delete Res -->
      <template>
        <v-dialog v-model="dialogDelete" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are sure want to delete ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogDelete = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="deletePlan">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- Submit Second modal -->
      <template>
        <v-dialog v-model="dialogSubmit" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <h2>Are you sure want to submit ?</h2>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialogSubmit = false"
                >No</v-btn
              >
              <v-btn color="info darken-1" text @click="submitHeader"
                >Yes</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <!-- View Plan Modal -->
      <template>
        <v-dialog
          v-model="dialogViewPlan"
          max-width="1500px"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <span class="headline"></span>
            </v-card-title>
            <v-card-text>
              <template v-if="dialogViewPlan">
                <TypViewModal :header_id="editedIndex" :typ_year="typ_year" />
              </template>
            </v-card-text>
          </v-card>
        </v-dialog>
      </template>

      <!-- TABLE-->
      <template>
        <v-simple-table class="mini-margin-top my_class">
          <template v-slot:default>
            <thead>
              <tr>
                <th>Actions</th>
                <th>Reference Number</th>
                <!-- <th>Region</th> -->
                <!-- <th>City</th> -->
                <!-- <th v-if="checkStatus">Current Chapter</th> -->
                <th v-if="checkStatus">Action Taken</th>
                <th v-if="cstatus == 0 ? true : false">Status</th>
                <th v-if="checkStatus">Remarks</th>
                <!-- <th>Year</th> -->
              </tr>
            </thead>
            <tbody>
              <tr v-for="res in allPlanList" :key="res.id">
                <td>
                  <!-- <v-menu offset-y dense max-height="200">
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            class="mx-1"
                            color="info"
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            depressed
                            x-small
                          >
                            <v-icon small>mdi-archive-eye-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>View</span>
                      </v-tooltip>
                    </template>
                    <v-list dense class="py-0">
                      <v-list-item
                        dense
                        v-for="index in 12"
                        :key="index"
                        @click="getView(res, index)"
                      >
                        Chapter {{ index }}
                      </v-list-item>
                    </v-list>
                  </v-menu> -->
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="info"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="
                          editedIndex = res.id;
                          typ_year = res.year;
                          dialogViewPlan = true;
                        "
                      >
                        <v-icon small>mdi-archive-eye-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>View</span>
                  </v-tooltip>
                  <v-menu offset-y dense v-if="checkStatus">
                    <template v-slot:activator="{ on: menu, attrs }">
                      <v-tooltip top>
                        <template v-slot:activator="{ on: tooltip }">
                          <v-btn
                            class="mx-1"
                            color="info"
                            v-bind="attrs"
                            v-on="{ ...tooltip, ...menu }"
                            depressed
                            x-small
                            @click="getEdit(res)"
                          >
                            <v-icon small>mdi-pencil-outline</v-icon>
                          </v-btn>
                        </template>
                        <span>Edit</span>
                      </v-tooltip>
                    </template>
                    <!-- <v-list dense class="py-0">
                      <v-list-item dense @click="getHeaderEdit(res, '2')">
                        Update Header
                      </v-list-item>
                      <v-list-item dense @click="getEdit(res)">
                        Update Content</v-list-item
                      >
                    </v-list> -->
                  </v-menu>

                  <v-tooltip top v-if="checkStatus">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        class="mx-1"
                        color="success"
                        depressed
                        x-small
                        v-bind="attrs"
                        v-on="on"
                        @click="getHeaderEdit(res, '3')"
                      >
                        <v-icon small>mdi-account-arrow-right</v-icon>
                      </v-btn>
                    </template>
                    <span>Process</span>
                  </v-tooltip>

                  <!-- <v-btn
                    class="mx-1"
                    v-if="checkStatus"
                    color="error"
                    depressed
                    fab
                    x-small
                    @click="getDelete(res)"
                  >
                    <v-icon>mdi-delete-alert-outline</v-icon>
                  </v-btn> -->
                </td>
                <td>{{ res.reference_number }}</td>
                <!-- <td v-if="checkStatus">{{ res.chapter }}</td> -->
                <td v-if="res.action_taken === '0' && checkStatus">
                  <v-chip small color="info" class="ma-2 ml-0"> None </v-chip>
                </td>
                <td v-else-if="res.action_taken === 'Return' && checkStatus">
                  <v-chip small color="error" class="ma-2 ml-0">
                    {{ res.action_taken }}
                  </v-chip>
                </td>
                <td v-if="cstatus == 0 ? true : false">
                  <v-chip
                    :color="setColor(res.status)"
                    class="ma-2 ml-0 white--text"
                    small
                  >
                    {{ res.status_name }}
                  </v-chip>
                </td>
                <td v-if="checkStatus">
                  <p>{{ res.remarks }}</p>
                </td>
                <!-- <td>{{ res.year }}</td> -->
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import TypViewModal from "../Include/typ_view_modal";
export default {
  name: "Eplan",
  components: {
    TypViewModal,
  },
  data: () => ({
    //table header states
    allLimit: [5, 10, 20, 50, 100],
    limitRow: [10],
    cstatus: "1",
    status: [
      { text: "All", value: "0" },
      { text: "Draft", value: "1" },
    ],
    Search: "",
    select_years: [],

    //-core states
    id: "",
    typ_year: "",
    user_region: "",
    user_region_id: "",
    cmbRegion: "",
    cmbProvince: "",
    cmbCity: "",
    txtReferenceNumber: "",
    txtStartYear: "",
    cmbPersonnel: "",
    remarks: "",
    txtWasteGen: "",
    txtPcg: "",
    origin: "",
    rowStatus: 0,
    uploads: [],
    route_order: "",
    items: [
      { text: "Real-Time", icon: "mdi-clock" },
      { text: "Audience", icon: "mdi-account" },
      { text: "Conversions", icon: "mdi-flag" },
    ],

    //-modals state
    editedIndex: -1,
    dialogAction: "",
    dialogDelete: false,
    dialogSubmit: false,
    dialogViewPlan: false,
    dialogAdd: false,

    //-validation states
    valid: true,
    requiredField: [(v) => !!v || "Field is required"],

    switch1: 1,
    actionChoice: [
      {
        value: 1,
        text: "Submit",
      },
      {
        value: 0,
        text: "Forward",
      },
    ],
    regionItem: [],
    provinceItem: [],
    cityItem: [],
    personnelItem: [],
    submitStat: "",
  }),
  methods: {
    ...mapActions([
      "fetchPlanList",
      "increment",
      "decrement",
      "searchPlanList",
      "deletePlantList",
      "fetchRegions",
      "fetchProvince",
      "fetchCities",
      "CreateHeaderPlan",
      "UpdateHeaderPlan",
      "fetchPerChapter",
      "getRegionInfo",
      "fetchPersonnel",
      "fetchStatus",
      "SubmitHeaderPlan",
      "uploadTypPerLine",
      "fetchTypAttach",
      "deleteFileVuex",
      "fetchSpecificPersonel",
    ]),
    ...mapMutations(["mutateHeader", "mutateCity", "chapter"]),
    //Table Header Functions
    nextFunction() {
      this.increment({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
      });
    },
    prevFunction() {
      this.decrement({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
      });
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
      });
    },
    limitFunction() {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
      });
    },
    statusFunction() {
      this.fetchPlanList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
      });
    },
    getSearch() {
      this.searchPlanList({
        txtsearch: this.Search,
        limit: this.limitRow,
        status: this.cstatus,
        new_app: 1,
      });
    },

    //Table Body Functions
    //--modals
    getDelete(res) {
      this.dialogDelete = true;
      this.editedIndex = res.id;
    },
    getHeaderEdit(res, action) {
      this.years();
      this.dialogAction = action;
      this.editedIndex = res.id;
      this.regionItem.push({
        value: res.region_id,
        text: res.rgnnam,
        value2: res.rgnnum,
      });
      this.provinceItem.push({
        value: res.province_id,
        text: res.prov_name,
      });
      this.cityItem.push({
        value: res.city_id,
        text: res.city_name,
      });
      // this.personnelItem.push({
      //   value: res.receiver,
      //   region: res.receiver_region,
      //   text: res.receiver_name,
      // });

      this.cmbRegion = res.region_id;
      this.cmbProvince = res.province_id;
      this.cmbCity = res.city_id;
      // this.cmbPersonnel = {
      //   text: res.receiver_name,
      //   value: res.receiver,
      //   region: res.receiver_region,
      // };

      if (res.central_to_lgu == 1) {
        this.fetchSpecificPersonel({
          text: res.sender_name,
          value: res.sender,
          region: res.sender_region,
        }).then(() => {
          this.personnelItem = this.allPersonnel;
          this.cmbPersonnel = this.allPersonnel[0];
          console.log(this.allPersonnel[0]);
        });
        this.submitStat = "3";
      } else {
        this.submitStat = "2";
        this.getPersonnel(res.region_id);
      }

      this.txtStartYear = parseInt(res.year);
      this.txtReferenceNumber = res.reference_number;

      this.getTypAttach(res.id, res.route_order);

      // this.remarks = res.remarks;
      // if (res.waste_kg_day != null) {
      //   this.txtWasteGen = this.numberWithCommas(res.waste_kg_day);
      // }
      this.rowStatus = parseInt(res.status);
      this.route_order = parseInt(res.route_order);
      this.txtPcg = res.pcg;
      sessionStorage.setItem("reference_number", res.reference_number);
      this.dialogAdd = true;
    },
    addNew() {
      sessionStorage.setItem("view", 0);
      this.dialogAction = "1";
      this.years();
      this.generateRefNo();

      this.fetchRegions().then(() => {
        this.regionItem = this.allRegions;
        this.cmbRegion = this.user_region_id;
        this.getProvince(this.user_region_id);
        this.getPersonnel(this.user_region_id);
        this.cmbProvince = sessionStorage.getItem("province_id");
        this.getCity();
        this.cmbCity = sessionStorage.getItem("city_id");
      });
    },
    confirmSubmit(res) {
      if (this.$refs.form.validate()) {
        this.dialogSubmit = true;
      }
    },

    //--Core Functions
    deletePlan() {
      this.deletePlantList({
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        id: this.editedIndex,
        new_app: 1,
      });
      this.dialogDelete = false;
    },
    insertHeader() {
      if (this.$refs.form.validate()) {
        this.CreateHeaderPlan({
          region: this.cmbRegion,
          province: this.cmbProvince,
          city: this.cmbCity,
          referenceNumber: this.txtReferenceNumber,
          year: this.txtStartYear,
          receiver: this.cmbPersonnel,
          remarks: this.remarks,
          pcg: this.txtPcg,
          created_by: sessionStorage.getItem("iis_employee_number"),
          created_by_name: sessionStorage.getItem("name"),
          created_date: this.getNow(),
          //waste_kg_day: this.txtWasteGen,
          offset: this.tgetCurrentrow,
          limit: this.limitRow,
          new_app: 1,
        });

        this.clearinput();
      }
    },
    updateHeader() {
      this.UpdateHeaderPlan({
        id: this.editedIndex,
        receiver: this.cmbPersonnel,
        remarks: this.remarks,
        pcg: this.txtPcg,
        waste_kg_day: this.txtWasteGen,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: this.rowStatus,
      });

      this.clearinput();
    },
    submitHeader() {
      this.SubmitHeaderPlan({
        id: this.editedIndex,
        receiver: this.cmbPersonnel,
        remarks: this.remarks,
        offset: this.tgetCurrentrow,
        limit: this.limitRow,
        new_app: 1,
        status: this.submitStat,
        route_order: this.route_order + 1,
        action_taken: "Submit",
        internal_action: 0,
        date: this.getNow(),
        action: this.getCondition,
        search_status: 1,
      });

      this.clearinput();
      this.dialogSubmit = false;
    },
    getEdit(res) {
      sessionStorage.setItem("view", 0);
      sessionStorage.setItem("pcg", res.pcg);
      sessionStorage.setItem("startingYear", res.year);
      sessionStorage.setItem("reference_number", res.reference_number);
      sessionStorage.setItem("header_id", res.id);

      // commit("chapter", res.chapter);
      // commit("mutateHeader", res.id);
      // commit("mutateCity", res.city_id);
      this.chapter(res.chapter);
      this.mutateHeader(res.id);
      this.mutateCity(res.city_id);
      this.$router.push({
        path: "plan",
        query: {
          chapter: res.chapter,
        },
      });
    },

    getView(res, chapter) {
      sessionStorage.setItem("view", 1);
      sessionStorage.setItem("reference_number", res.reference_number);
      this.$router.push({
        path: "plan",
        query: {
          city: res.city_id,
          header_id: res.id,
          chapter: chapter,
        },
      });
    },

    //Additional Method
    getProvince(region) {
      this.cmbProvince = [];
      this.cmbCity = [];

      if (region) {
        this.fetchProvince(region).then(() => {
          this.provinceItem = this.allProvinces;
        });
      }
    },
    getPersonnel(region) {
      this.cmbPersonnel = [];

      if (region) {
        this.fetchPersonnel({
          reg_id: this.user_region_id,
          user_role: parseInt(sessionStorage.getItem("access")),
          action: 1,
          user_id: sessionStorage.getItem("iis_employee_number"),
        }).then(() => {
          this.personnelItem = this.allPersonnel;
          this.cmbPersonnel = this.allPersonnel[0];
        });
      }
    },
    getCity() {
      this.cmbCity = [];
      if (this.cmbProvince) {
        this.fetchCities(this.cmbProvince).then(() => {
          this.cityItem = this.allCities;
        });
      }
    },
    isNum(value, thisModel, withComa) {
      var isValid = /^[0-9,.]*$/.test(value);
      if (isValid) {
        value = value.replace(/,/g, ""); // remove commas from existing input
        if (withComa) {
          this[thisModel] = this.numberWithCommas(value); // add commas back in
        }
      } else {
        this[thisModel] = "";
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    getNow() {
      const today = new Date();
      const date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      const time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      const dateTime = date + " " + time;
      return dateTime;
    },
    years() {
      let year = new Date().getFullYear();
      this.select_years = Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
      return Array.from(
        { length: 10 },
        (value, index) => parseInt(year) + index
      );
    },
    generateRefNo() {
      let rand = Math.floor(100000 + Math.random() * 900000);
      let reg = sessionStorage.getItem("cregion");
      let year = new Date().getFullYear();
      this.txtReferenceNumber = reg + "-" + year + "-" + rand;
    },
    clearinput() {
      this.txtReferenceNumber = "";
      this.txtStartYear = "";
      this.cmbRegion = "";
      this.cmbProvince = "";
      this.cmbCity = "";
      this.cmbPersonnel = "";
      this.remarks = "";
      this.txtPcg = "";
      this.txtWasteGen = "";
      this.uploads = [];
      this.dialogAdd = false;
      this.editedIndex = -1;
    },
    setColor(res) {
      if (res == "1") {
        return "green";
      } else if (res == "2") {
        return "blue";
      } else if (res == "3") {
        return "teal  ";
      } else if (res == "4") {
        return "brown";
      } else if (res == "5") {
        return "brown";
      }
    },

    //File Upload
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      this.uploads = Object.entries(files).map((e, i) => e[1]);
    },

    removeFile(i) {
      this.uploads.splice(i, 1); // 2nd parameter means remove one item only
    },

    getTypAttach(id, route_order) {
      this.fetchTypAttach({
        header_id: id,
        route: route_order,
        user: sessionStorage.getItem("iis_employee_number"),
      }).then(() => {
        //this.setAttachment();
      });
    },

    uploadSpecificFile(item, i) {
      var formData = new FormData();
      formData.append("files[]", item);
      formData.append("header_id", this.editedIndex);
      formData.append("route", parseInt(this.route_order));
      formData.append("user", sessionStorage.getItem("iis_employee_number"));
      formData.append(
        "reference_number",
        sessionStorage.getItem("reference_number")
      );
      this.uploadTypPerLine(formData).then(() => {
        this.removeFile(i);
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    deleteFile(item) {
      this.deleteFileVuex(item).then(() => {
        this.getTypAttach(this.editedIndex, this.route_order);
      });
    },

    convertFile(data) {
      let newAllAttach = JSON.stringify(data);
      let new_data = JSON.parse(newAllAttach);
      new_data = new_data.map((e, i) => ({
        ...e,
        file_path_name: JSON.parse(e.file_path_name),
      }));
      return new_data;
    },
  },
  computed: {
    ...mapGetters([
      "allPlanList",
      "tgetCurrentrow",
      "allRegions",
      "allProvinces",
      "allCities",
      "allPersonnel",
      // "status",
      "allTypAttach",
    ]),
    formTitle() {
      return this.editedIndex === -1 ? "Add 10 Year Plan" : "Edit 10 Year Plan";
    },
    checkStatus() {
      return this.cstatus === "1" ? true : false;
    },

    buttonTitle() {
      return this.switch1 == 0 ? "Forward" : "Submit";
    },
  },
  created() {
    this.user_region = sessionStorage.getItem("cregion");
    this.user_region_id = sessionStorage.getItem("region_id");
    this.origin = sessionStorage.getItem("origin");
    // this.fetchPlanList({
    //   offset: this.tgetCurrentrow,
    //   limit: this.limitRow,
    //   status: this.cstatus,
    //   new_app: 1,
    // });
    // this.fetchStatus();
  },
  watch: {},
};
</script>